	module.exports = {"data":{"post":{"html":"<p>Yesterday a 500 pound American bomb from World War II was disarmed just across the street from my office. It was discovered by construction workers at the Bavaria St. Pauli brewery the day before. All buildings within 200 yards were evacuated, all streets were closed within 500 yards, as were the U- and S-Bahn. The red-light district on the Reeperbahn was deserted. We weren't allowed to leave our building for 3 hours. And production was halted at the brewery for an entire day. </p>\n<p>Hamburg was very heavily bombed during the war, and out of the hundreds of thousands of bombs that were dropped, some 10% were duds. Even today, a new one is discovered every month or so, usually at construction sites. A bomb survey is required for any building permit in Hamburg. Aerial photos taken by the US and British air forces after the bombing raids are examined... the duds are visible in the photos as pinholes without craters. However, the bomb yesterday was not discovered by a photograph, but by an unlucky bulldozer driver.</p>\n<p>The preparations took over 24 hours, but the bomb itself was disarmed in just 15 minutes. In theory, one just needs to unscrew the detonator with a decent wrench. In practice, nothing that has been underground for 60 years comes apart very easily. I'm certainly happy that I don't work for Hamburg's 'Kampfmittelräumdienst'.</p>\n<p>And if a rich country like Germany is still cleaning up from a war 60 years ago, how long will it take for the scars to heal in Vietnam, or Iraq, or Bosnia, or Kosovo, or Rwanda, or Angola, or ...?</p>","fields":{"title":"Bomb Scare","date":"02 Nov 2000","url":"https://papascott.de/2000-11-02-bomb-scare/"}}},"pathContext":{"slug":"/2000-11-02-bomb-scare/","prev":{"fields":{"title":"Halloween","date":"31 Oct 2000","slug":"/2000-10-31-halloween/"}},"next":{"fields":{"title":"Vision Thing","date":"04 Nov 2000","slug":"/2000-11-04-vision-thing/"}}}}

